.home__position {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.home__main {
  width: 40.648148148148145vh;
  padding: 1.8518518518518516vh;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.125) 100%);
  border-radius: 1.8518518518518516vh;
}
.home__main_img {
  width: 40.648148148148145vh;
  height: 14.444444444444443vh;
  border-radius: 0.9259259259259258vh;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 1.8518518518518516vh;
}
.home__main_grid {
  display: grid;
  grid-template-columns: 40.648148148148145vh;
  row-gap: 1.8518518518518516vh;
}
.home__main_box_info {
  display: flex;
  height: 5.37037037037037vh;
  align-items: center;
  padding-left: 1.574074074074074vh;
  padding-right: 1.574074074074074vh;
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.2);
  border-radius: 0.6481481481481481vh;
}
.home__main_box_info_note {
  font-family: 'Gotham Pro Regular';
  font-size: 1.3888888888888888vh;
  color: rgba(255, 255, 255, 0.5);
}
.home__main_box_info_text {
  font-family: 'Gotham Pro Medium';
  font-size: 1.3888888888888888vh;
  color: #ffffff;
  margin-left: 1.3888888888888888vh;
}
.home__main_box_buttons {
  display: flex;
  flex-direction: row;
  margin-top: 1.8518518518518516vh;
}
.home__main_box_button {
  width: 19.537037037037035vh;
  height: 5.555555555555555vh;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 0.6481481481481481vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Gotham Pro Regular';
  font-size: 1.3888888888888888vh;
  color: #ffffff;
  margin-right: 1.8564814814814814vh;
}
.home__main_box_button:last-child {
  margin-right: 0;
}
.home__main_grid4 {
  display: grid;
  grid-template-columns: repeat(2, 19.537037037037035vh);
  column-gap: 1.8518518518518516vh;
  row-gap: 1.8518518518518516vh;
}
.home__main_box_info_button {
  font-family: 'Gotham Pro Regular';
  font-size: 1.3888888888888888vh;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.2);
  border-radius: 0.6481481481481481vh;
}
.home__main_box_info_input {
  font-family: 'Gotham Pro Regular';
  font-size: 1.3888888888888888vh;
  color: #ffffff;
  width: 100%;
}
.home__main_box_info_input::placeholder {
  font-family: 'Gotham Pro Regular';
  font-size: 1.3888888888888888vh;
  color: #ffffff;
}
.home__main_box_input {
  display: flex;
  height: 5.555555555555555vh;
  align-items: center;
  padding-left: 1.574074074074074vh;
  padding-right: 1.574074074074074vh;
  border-radius: 0.6481481481481481vh;
  background: rgba(255, 255, 255, 0.15);
  font-family: 'Gotham Pro Regular';
  font-size: 1.3888888888888888vh;
  color: #ffffff;
}
@media (aspect-ratio: 577/540) {
  /* 1154 x 1080 */
  .home__main {
    transform: scale(0.8) translate(0, 0);
  }
}
