@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.fade_zone-enter-active {
  opacity: 1;
  animation: fadeIn 1s;
}

.fade_zone-enter {
  opacity: 0;
}
.fade_zone-exit {
  opacity: 1;
}
.fade_zone-exit-active {
  opacity: 0;
  animation: fadeOut 1s;
}
