.notification-timer {
  font-family: 'Choujikuu';
  color: #48b9f2;
  font-size: 1.1851851851851851vh;
  width: 100%;
  margin-left: 92%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-top: 2%;
}

.notification-timer::after {
  content: '';
  margin-top: 16%;
  font-size: 0.5925925925925926vh;
}

.notification-timer-line {
  width: 100%;
  background-color: #48b9f2;
  height: 4%;
  position: absolute;
  z-index: 1;
}

.notification-container {
  outline: none;
}

.notification-content-text {
  font-family: 'Roboto';
  font-size: 12;
  padding-left: 9.5%;
  padding-right: 9.5%;
}
