.license-main {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
}

.lic-box {
  width: 49.25925925925926vh;
  height: 61.388888888888886vh;
  background: url(../img/bg.png) no-repeat;
  border-width: 0.13888888888888887vh;
  outline-offset: -0.9259259259259258vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
}

.lic-in-box {
  width: 30.555555555555554vh;
  height: 54.629629629629626vh;
  display: flex;
  flex-direction: row;
}

.lic-in-box .lic-left {
  width: 19.166666666666664vh;
  height: 31.944444444444443vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.lic-in-box .lic-right {
  width: 30.555555555555554vh;
  height: 54.629629629629626vh;
  display: flex;
  flex-direction: column;
  position: absolute;
}

.lic-photo_player {
  width: 12.222222222222221vh;
  height: 12.222222222222221vh;
  z-index: 1;
  position: absolute;
  left: 32%;
  top: 6%;
  border-radius: 0.9259259259259258vh;
}

.lic-photo_player .lic-img {
  position: absolute;
  border-radius: 0.9259259259259258vh;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  max-height: 100%;
  max-width: 100%;
  display: block;
  width: 12.222222222222221vh;
  height: 12.222222222222221vh;
  border-bottom: 0.9259259259259258vh solid rgba(0, 31, 60, 0.42);
  border-bottom-left-radius: 0.9259259259259258vh;
  border-bottom-right-radius: 0.9259259259259258vh;
}

.lic-right .lic-name {
  font-family: 'RobotoLight';
  font-size: 1.8518518518518516vh;
  color: #48b9f2;
  margin-top: 3.4259259259259256vh;
  margin-left: 1.759259259259259vh;
}

.lic-right .lic-info {
  width: inherit;
  border-top: 0.09259259259259259vh solid rgb(255, 255, 255, 0.051);
  margin-top: 17.59259259259259vh;
}

.pl-lic-grow {
