/*Position*/
.notifi-position-leftTop {
  justify-content: flex-start;
  align-items: flex-start;
}

.notifi-position-left {
  justify-content: flex-start;
  align-items: center;
}

.notifi-position-leftBottom {
  justify-content: flex-start;
  align-items: flex-end;
}

.notifi-position-centerTop {
  justify-content: center;
  align-items: flex-start;
}

.notifi-position-center {
  justify-content: center;
  align-items: center;
}

.notifi-position-centerBottom {
  justify-content: center;
  align-items: flex-end;
}

.notifi-position-rightTop {
  justify-content: flex-end;
  align-items: flex-start;
}

.notifi-position-right {
  justify-content: flex-end;
  align-items: center;
}

.notifi-position-rightBottom {
  justify-content: flex-end;
  align-items: flex-end;
}

.notifi-type {
  display: flex;
  width: -webkit-fill-available;
  height: -webkit-fill-available;
  position: absolute;
}

.notifi-text-box {
  width: calc(100% - 2.222222222222222vh);
  height: calc(79% - 2.222222222222222vh);
  overflow: scroll;
  margin-top: 3%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'RobotoLight';
  line-height: 1.5;
  font-size: 1.7777777777777777vh;
  letter-spacing: 0.07407407407407407vh;
  font-size: 1.4814814814814814vh;
  color: #fff;
}

.notifi-text-box-z {
  width: calc(100% - 2.222222222222222vh);
  height: calc(79% - 2.222222222222222vh);
  overflow: scroll;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'RobotoLight';
  line-height: 1.5;
  font-size: 1.7777777777777777vh;
  letter-spacing: 0.07407407407407407vh;
  font-size: 1.4814814814814814vh;
  color: #fff;
}

.notifi-btn {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: row-reverse;
  align-items: center;
  font-family: 'Choujikuu';
  color: rgb(0, 0, 0);
  font-size: 1.2962962962962963vh;
}

.n-btn-box:last-child {
  margin-left: 0vh;
}

.notifi-head {
  display: flex;
  margin-top: 0.7407407407407407vh;
}

.notifi-title {
  width: auto;
  height: 2.222222222222222vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Gotham Pro Bold';
  color: #fff;
  font-size: 3.7037037037037033vh;
  font-weight: 500;
  padding-right: 0.7407407407407407vh;
  max-width: 90%;
}

.notifi-head .notifi-close {
  margin-top: 0vh;
}

.notifi-linear-left {
  position: absolute;
  width: 0.5555555555555555vh;
  height: 11.11111111111111vh;
  background: rgb(72, 185, 242);
  left: -0.37037037037037035vh;
  top: 0.4629629629629629vh;
  z-index: 1;
}

.notifi-linear-rightbottom {
  position: absolute;
  width: 0.5555555555555555vh;
  height: 11.11111111111111vh;
  background: rgb(72, 185, 242);
  right: -0.37037037037037035vh;
  bottom: 0vh;
  z-index: 1;
}

.notifi-linear-left-two {
  position: absolute;
  width: 0.5555555555555555vh;
  height: 11.11111111111111vh;
  background: rgb(72, 185, 242);
  left: -0.37037037037037035vh;
  bottom: 1.0185185185185184vh;
}

.notifi-linear-bottom {
  position: absolute;
  width: 10vh;
  height: 0.5555555555555555vh;
  background: rgb(72, 185, 242);
  left: 0.9259259259259258vh;
  bottom: -0.37037037037037035vh;
}

.notifi-linear-bottom-littel {
  position: absolute;
  width: 0.5555555555555555vh;
  height: 0.5555555555555555vh;
  background: rgb(72, 185, 242);
  left: 11.388888888888888vh;
  bottom: -0.37037037037037035vh;
}

.notifi-title-text {
  width: auto;
  height: 2.222222222222222vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Gotham Pro Medium';
  color: #fff;
  font-size: 2.314814814814815vh;
  line-height: 2.222222222222222vh;
  padding-right: 0.7407407407407407vh;
  max-width: 31.48148148148148vh;
}

.notifi-icons {
  width: 1.8518518518518516vh;
  height: 1.8518518518518516vh;
  margin: 0;
  padding-right: 0.4629629629629629vh;
}

/**/

.alert-access-btn {
  background: #36cc5c;
  width: auto;
  border-radius: 0;
}

.alert-decline-btn {
  background: #ed213a;
  width: auto;
  border-radius: 0;
}
