.hmenu__gunshop__catalog {
  display: grid;
  grid-template-columns: repeat(2, 34.07407407407407vh);
  grid-template-rows: min-content;
  grid-auto-rows: max-content;
  grid-column-gap: 1.6666666666666665vh;
  grid-row-gap: 1.574074074074074vh;
  overflow-y: auto;
  margin-left: 3.7037037037037033vh;
  margin-top: 3.7037037037037033vh;
  padding-right: 1.4814814814814814vh;
}

.hmenu__gunshop__catalog.cols-3 {
  grid-template-columns: 33% 33% 33%;
}

.hmenu__gunshop__catalog__container {
  overflow-y: auto;
  /* padding-right: 1%; */
  display: flex;
  background: rgba(0, 0, 0, 0.5);
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.5);
  border-radius: 1.8518518518518516vh;
  margin-right: 3.7037037037037037vh;
  /* padding: 1.5%; */
  width: 77.22222222222221vh;
  height: 81.48148148148148vh;
}

.hmenu__gunshop__catalog__item {
  border-radius: 0.9259259259259258vh;
  display: flex;
  padding: 2.222222222222222vh;
  flex-direction: column;
  cursor: pointer;
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.5);
  height: fit-content;
}

.hmenu__gunshop__catalog__item_active {
  background: rgba(0, 0, 0, 0.5);
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.5);
  box-sizing: border-box;
  border-radius: 0.9259259259259258vh;
  display: flex;
  padding: 2.222222222222222vh;
  flex-direction: column;
  cursor: pointer;
  height: fit-content;
}

.hmenu__gunshop__catalog__item__img {
  width: 55%;
  object-fit: contain;
}

.hmenu__gunshop__catalog__item__img__container {
  margin-top: 10%;
  margin-bottom: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hmenu__gunshop__catalog__item__name {
  font-family: 'Gotham Pro Medium';
  font-size: 2.962962962962963vh;
}

.hmenu__gunshop__catalog::-webkit-scrollbar {
  width: 0.5925925925925926vh;
}

.hmenu__gunshop__catalog::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.35);
  border-radius: 1.3888888888888888vh;
}

.hmenu__gunshop__catalog::-webkit-scrollbar-thumb {
  background: #ffffff;
  border-radius: 1.3888888888888888vh;
}

.hmenu__gunshop__catalog__item__info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.hmenu__gunshop__catalog__item__calibr {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.hmenu__gunshop__catalog__item__price {
  font-family: 'Gotham Pro Medium';
  font-size: 1.8518518518518519vh;
}

.hmenu__gunshop__catalog__item__calibr__text {
  font-family: 'Gotham Pro Regular';
  font-size: 1.2962962962962963vh;
  letter-spacing: 0.07407407407407407vh;
}

.hmenu__gunshop__catalog__item__calibr__name {
  font-family: 'Gotham Pro Medium';
  font-size: 1.3888888888888888vh;
}

.hmenu__gunshop__catalog__item__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: visible;
  height: 2.222222222222222vh;
}

.hmenu__gunshop__catalog__item__sale {
  position: relative;
  padding: 0.5555555555555555vh;
  /*right: 50%;*/
  bottom: 3.148148148148148vh;

  /*padding-top: 3%;
    padding-bottom: 3%;
    padding-left: 1.9259259259259258vh;
    padding-right: 1.9259259259259258vh;*/
  border-radius: 0.5925925925925926vh;
  font-family: 'Gotham Pro Regular';
  background: linear-gradient(105.63deg, rgba(255, 64, 57, 0) -80.94%, rgba(255, 64, 57, 0.45) 275.52%);
  border: 0.046296296296296294vh solid #ff4039;
  border-radius: 0.4629629629629629vh;
}
