:root {
  --text-buttons-color: #2a56c6;
  --icons-color: #1c3aa9;
}

.calls__actions__item:hover {
  background: #0000000f;
}

.calls__actions__item {
  width: 100%;
  padding-left: 7%;
  padding-right: 7%;
  padding-top: 2%;
  padding-bottom: 2%;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 33%;
}

.calls__actions__item__icon {
  margin-right: 8%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calls__actions {
  margin-top: 2%;
  margin-bottom: 2%;
  height: 23%;
  display: flex;
  flex-direction: column;
}

.calls__navbar__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.calls__navbar__item__text-active {
  color: #0a84ff;
  font-family: 'SF Pro Regular';
  font-size: 1.111111111111111vh;
  margin-top: 0.4629629629629629vh;
}

.calls__navbar__item__icon-active {
  filter: invert(37%) sepia(98%) saturate(4277%) hue-rotate(196deg) brightness(107%) contrast(109%);
}

.calls__navbar__item__text {
  font-family: 'SF Pro Regular';
  font-size: 1.111111111111111vh;
  color: #757575;
  margin-top: 0.4629629629629629vh;
}

.calls__navbar {
  margin-top: auto;
  background: rgba(22, 22, 22, 0.75);
  box-shadow: 0vh -0.046296296296296294vh 0vh rgba(255, 255, 255, 0.16);
  backdrop-filter: blur(7.550787037037037vh);
  width: 100%;
  height: 9.333333vh;
  justify-content: space-around;
  align-items: center;
  display: flex;
  padding-bottom: 10%;
}

.calls__actions__item__text {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: normal;
  font-size: 1.2037037037037037vh;
  color: rgba(0, 0, 0, 0.87);
  display: flex;
}
.fix_image_nav_bar {
  width: 3.7037037037037037vh;
  height: 3.7037037037037037vh;
}
.calls_button_img {
  width: 2.962962962962963vh;
  height: 2.962962962962963vh;
  background-repeat: no-repeat;
  background-size: cover;
  filter: invert(100%) sepia(0%) saturate(1%) hue-rotate(134deg) brightness(106%) contrast(101%);
}
