:root {
  --bg-color: #0c0c0c;
}

ion-icon {
  cursor: pointer;
}

.menu-box-content {
  display: flex;
  flex: 1 1 0%;
  /*background-color: rgb(12, 12, 12);*/
  flex-direction: column;
  opacity: 0.88;
  outline: none;
  width: 43.79629629629629vh;
  background: rgba(0, 0, 0, 0.7);
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.5);
  border-radius: 1.8518518518518516vh;
  padding: 2.962962962962963vh;
}

.menu-box-footer {
  background: rgba(0, 0, 0, 0.6);
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.5);
  border-radius: 1.8518518518518516vh;
  padding: 2.962962962962963vh;
  margin-top: 1.4814814814814814vh;
}

.logo-info__text-bold {
  font-family: 'Century Gothic';
  font-weight: bold;
}

.logo-info__text-normal {
  font-family: 'Century Gothic';
}

.menu-box-content div,
label {
  outline: none;
}

.menu-box {
  display: flex;
  position: absolute;
  min-height: 37.68518518518518vh;
  right: 3.7037037037037033vh;
  top: 8.333333333333332vh;
  flex-direction: column;
}

.checkbox-container {
  position: relative;
  cursor: pointer;
}

.checkbox-container input {
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  height: 1.4814814814814814vh;
  width: 1.4814814814814814vh;
  border: 0.046296296296296294vh solid #ffffff;
  border-radius: 0.37037037037037035vh;
}

.checkmark:hover {
  background-color: #ccc;
}

.checkbox-container input:checked ~ .checkmark {
  background-color: #fff;
}

.checkmark:after {
  content: '';
  display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-container .checkmark:after {
  width: 0.37037037037037035vh;
  height: 0.7407407407407407vh;
  border: solid #000;
  border-width: 0 0.18518518518518517vh 0.18518518518518517vh 0;
  margin-bottom: 0.18518518518518517vh;
  margin-left: 0.09259259259259259vh;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menuContainer {
  outline: none;
}

.menu-item {
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  border-radius: 0.6481481481481481vh;
  margin-bottom: 1.037037037037037vh;
  border: 0.046296296296296294vh solid rgba(255, 255, 255, 0.25);
}

.menu-item-inverted {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 0.6481481481481481vh;
  margin-bottom: 1.037037037037037vh;
  border: 0.046296296296296294vh solid transparent;
}

.list-arrow-icon {
  width: 2.222222222222222vh;
  height: 1.8518518518518516vh;
  display: flex;
  alignitems: center;
  justify-content: center;
}

.menu-item-inverted span {
}

.menu-item:hover {
  background: rgba(255, 255, 255, 0.05);
  transition: 0.4s ease-in-out;
}
.img_icon_style {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
