.calls__keyboard {
  padding-left: 10%;
  padding-right: 10%;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.calls__keyboard__row {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  margin-bottom: 1.4814814814814814vh;
}

.calls__keyboard__row__number {
  justify-content: center;
  align-items: center;
  flex-direction: column;
  display: flex;
  background: rgb(38, 38, 38);
  border-radius: 50%;
  height: 5.555555555555555vh;
  width: 5.555555555555555vh;
}

.calls__keyboard__row__number__text {
  color: #fff;
  font-size: 2.4074074074074074vh;
  font-family: 'SF Pro Regular';
}

.calls__keyboard__row__number__subtext {
  font-size: 0.9259259259259258vh;
  font-family: 'SF Pro Regular';
  color: #fff;
  letter-spacing: 0.09259259259259259vh;
  text-transform: uppercase;
}
