.chevron-top::before {
  border-style: solid;
  border-width: 0.05em 0.05em 0 0;
  content: '';
  display: inline-block;
  height: 0.4em;
  position: relative;
  vertical-align: top;
  width: 0.4em;
  top: 0.4em;
  transform: rotate(-45deg);
}
